<template>
  <div>
    <h1>Anotações sobre as capivaras (Hydrochoerus hydrochaeris)</h1>
    
    <div>
      <label for="filter-habitat">Filtrar por habitat:</label>
      <select id="filter-habitat" v-model="selectedHabitat" @change="filtrarPorHabitat">
        <option value="Todos">Todos</option>
        <option value="Lago Sul">Lago Sul</option>
        <option value="Floresta Oeste">Floresta Oeste</option>
        <option value="Recinto Norte">Recinto Norte</option>
      </select>
    </div>

    <button @click="abrirFormularioAdicao">{{ mostrarFormulario ? 'Fechar' : 'Adicionar Nova Capivara' }}</button>

    <div v-if="mostrarFormulario">
      <h2>{{ isEditing ? 'Editar Capivara' : 'Adicionar Nova Capivara' }}</h2>
      <input v-model="capivaraEditando.Nome" placeholder="Nome" :disabled="isEditing"/>
      <input v-model="capivaraEditando.Idade" placeholder="Idade" />
      <input v-model="capivaraEditando.Peso" placeholder="Peso" />
      <input v-model="capivaraEditando['Status de Saúde']" placeholder="Status de Saúde" />
      <input v-model="capivaraEditando.Habitat" placeholder="Habitat" />
      <input v-model="capivaraEditando.Comportamento" placeholder="Comportamento" />
      <input v-model="capivaraEditando.Dieta" placeholder="Dieta" />
      <input v-model="capivaraEditando.Observações" placeholder="Observações" />
      <button @click="isEditing ? salvarCapivara() : adicionarCapivara()">{{ isEditing ? 'Salvar' : 'Adicionar' }}</button>
    </div>

    <div v-for="capivara in capivarasFiltradas" :key="capivara.Nome" class="capivara-card">
      <h2>{{ capivara.Nome }}</h2>
      <p><strong>Idade:</strong> {{ capivara.Idade }} anos</p>
      <p><strong>Peso:</strong> {{ capivara.Peso }} kg</p>
      <p><strong>Status de Saúde:</strong> {{ capivara['Status de Saúde'] }}</p>
      <p><strong>Habitat:</strong> {{ capivara.Habitat }}</p>
      <p><strong>Comportamento:</strong> {{ capivara.Comportamento }}</p>
      <p><strong>Dieta:</strong> {{ capivara.Dieta }}</p>
      <p><strong>Observações:</strong> {{ capivara.Observações || 'Sem observações' }}</p>
      <button @click="editarCapivara(capivara)">Editar</button>
      <button @click="excluirCapivara(capivara.Nome)">Excluir</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      capivaras: [],
      capivaraEditando: {
        Nome: '',
        Idade: '',
        Peso: '',
        'Status de Saúde': '',
        Habitat: '',
        Comportamento: '',
        Dieta: '',
        Observações: '',
      },
      mostrarFormulario: false,
      isEditing: false,  // Define se está em modo de edição
      selectedHabitat: 'Todos',
    };
  },
  computed: {
    capivarasFiltradas() {
      if (this.selectedHabitat === 'Todos') {
        return this.capivaras;
      }
      return this.capivaras.filter(capivara => capivara.Habitat === this.selectedHabitat);
    },
  },
  created() {
    this.fetchCapivaras();
  },
  methods: {
    async fetchCapivaras() {
      try {
        const response = await axios.get('http://localhost:5001/capivaras');
        this.capivaras = response.data;
      } catch (error) {
        console.error('Erro ao buscar capivaras:', error);
      }
    },
    abrirFormularioAdicao() {
      this.mostrarFormulario = !this.mostrarFormulario;
      this.isEditing = false;
      this.limparFormulario();
    },
    limparFormulario() {
      this.capivaraEditando = {
        Nome: '',
        Idade: '',
        Peso: '',
        'Status de Saúde': '',
        Habitat: '',
        Comportamento: '',
        Dieta: '',
        Observações: '',
      };
    },
    async adicionarCapivara() {
      try {
        await axios.post('http://localhost:5001/capivaras', this.capivaraEditando);
        this.fetchCapivaras(); // Atualizar a lista de capivaras depois de adicionar
        this.mostrarFormulario = false; // Fechar o formulário após adicionar
        this.limparFormulario(); // Limpar o formulário
      } catch (error) {
        console.error('Erro ao adicionar capivara:', error);
      }
    },
    async salvarCapivara() {
      try {
        await axios.put(`http://localhost:5001/capivaras/${this.capivaraEditando.Nome}`, this.capivaraEditando);
        this.isEditing = false;
        this.fetchCapivaras();
        this.mostrarFormulario = false; // Fechar o formulário após salvar
        this.limparFormulario(); // Limpar o formulário
      } catch (error) {
        console.error('Erro ao salvar capivara:', error);
      }
    },
    editarCapivara(capivara) {
      this.capivaraEditando = { ...capivara };
      this.mostrarFormulario = true;
      this.isEditing = true;
    },
    async excluirCapivara(nome) {
      try {
        await axios.delete(`http://localhost:5001/capivaras/${nome}`);
        await axios.get('/api/capivaras');

        this.fetchCapivaras();
      } catch (error) {
        console.error('Erro ao excluir capivara:', error);
      }
    },
  },
};
</script>

<style scoped>
.capivara-card {
  background-color: #f9f9f9;
  padding: 15px;
  margin: 10px 0;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
button {
  margin: 5px;
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: #0056b3;
}
</style>
